	module.exports = {"data":{"contentfulChapitre":{"titreChapitre":"Chapitre 5 - La première mission","texte":{"childMarkdownRemark":{"html":"<p>Après les ajouts des armes que j’affectionnais, le poing américain a été rajouté à mon poing gauche, trois dagues sont stockées dans chacun de mes avant-bras et quatre sont placées dans mon dos, juste au-dessus des fesses, le pistolet était trop dur à cacher au niveau de mes avant-bras, il fût donc installé sur le côté de ma cuisse droite. Les grenades flash sont installées au niveau de mes arrière-bras permettant un envoi facile des grenades. Le fusil sniper fût modifié pour être montable et démontable à volonté. Darakei entre dans ma chambre.</p>\n<p>Darakei : Alors, tout est bien installé?<br>\nWamien : Oui, tout est très bien installé, un peu dur de prendre le sniper, mais ça peut aller.<br>\nDarakei : Tu veux qu’on te rajoute de la flexibilité, qu’on t’intègre un petit mouvement pour qu’il soit plus facile à prendre?<br>\nWamien : La flexibilité ne serait pas de refus.<br>\nDarakei : Parfait, on va t’arranger ça dès que tu veux.<br>\nWamien : Oh prenez votre temps, mais au juste pourquoi vous m’avez ajouté tout ça, je pars en mission?<br>\nDarakei : Exactement.<br>\nWamien : Oh. </p>\n<p>Je souris un peu content de pouvoir sortir enfin du labo.</p>\n<p>Darakei : On va te faire un résumé de tes tâches dans une heure, alors repose-toi en attendant.</p>\n<p>Darakei quitte la pièce.</p>\n<p>Wamien : Enfin, je vais pouvoir sortir d’ici.</p>\n<p>Je me place devant le miroir pour observer mon corps. Je me rends donc compte que j’ai grandi d’au moins 10 centimètres selon ma grandeur d’avant. Ma « musculature » aussi est plus imposante malgré le fait que mon corps n’a pas besoin de toutes ces plaques de métal pour être qui je suis. Je sais que j’ai encore toute ma tête et qu’ils sont encore loin de me contrôler. J’attendrai le bon moment pour m’enfuir, parce qu’ils doivent bien m’avoir foutu un traceur quelque part. L’heure passa assez rapidement parce que je révisais les mouvements que j’avais appris, enfin, télécharger dans ma mémoire. Je sors de ma chambre et une jeune femme me conduit à la salle de rencontres pour m’expliquer ce que je vais devoir faire. J’arrive dans la pièce, Darakei et deux autres personnes sont présentes.</p>\n<p>Darakei : Je vous présente, Wamien Mata, le premier cyborg à être entièrement autonome de ses faits et gestes.<br>\nWamien : Heu bonjour.<br>\nHomme chauve : C’est donc sur ce projet que vous fantasmez depuis cinq ans.<br>\nDarakei : Précisément, Monsieur Hikino, dis Darakei calmement. Et permettez-moi de préciser que ce n’est pas un fantasme, mais un rêve qui est devenu réalité.<br>\nHikino : Mais les rêves sont pour les enfants, Monsieur Drumel, s’exclama en riant Monsieur Hikino.</p>\n<p>La femme aux cheveux noirs et aux lunettes de fond de bouteille commence à parler.</p>\n<p>Femme : Monsieur Drumel, nous ne sommes pas au moment de dîner, vous savez? Ce jeune homme est là pour une raison précise.<br>\nDarakei : Oui. Donc, tu vas devoir aller dans le royaume voisin, le royaume de Guifon, un homme du nom de Mickaël Stephan vole depuis près de dix ans près de cinq milles personnes en leur faisant signer un contrat d’investissement de dix mille dollars. Nous avons retrouvé sa trace dans ce royaume. Nous avons besoin de deux choses. La liste de tous les noms des gens qu’il a fraudés et de combien et aussi qu’il se fasse attraper par les forces de l’ordre, autant par la police du royaume voisin que la police de notre royaume. Et important à dire, on le veut vivant et sans blessures compris?<br>\nWamien : Très bien compris, je trouverai cet homme sans problèmes.<br>\nDarakei : Oh et tu auras besoin de lire ça avant de partir. Il fit glisser une clé USB jusqu’à moi. Cette clé USB te permettra de passer la frontière et de prendre toutes les informations dont tu as besoin au sujet de Monsieur Stephan.<br>\nWamien : Parfait. Je tourne les talons et pars vers la sortie du labo.</p>\n<p>Personne ne me retenait de passer. Je sortis du labo. Ah l’air pur, j’ai l’impression que ça fait 300 ans que je n’ai pas respiré du bon air. Je me retrouve rapidement dans la ville et me dirige vers la frontière où je montre la clé USB, le garde la vérifie, me la remets et je passe la barrière. Ma mission est commencée.</p>"}},"chapitreAvant":"chapitre-4-l-entrainement","chapitreApres":"chapitre-6-la-rencontre","nomRoman":"le-premier-cyborg-tome-1","slug":"chapitre-5-la-premiere-mission","codeChapitre":"H0001C005","equivalentUrl":"/stories/chapter/chapter-5-first-mission"}},"pathContext":{"slug":"chapitre-5-la-premiere-mission"}}

